import React from 'react';
import { ArrowLeft, Brain } from 'lucide-react';
import { CoinCounter } from '../atoms/CoinCounter';

interface ModuleHeaderProps {
  title: string;
  description: string;
  coinCount: number;
  onBack: () => void;
  className?: string;
}

export const ModuleHeader: React.FC<ModuleHeaderProps> = ({
  title,
  description,
  coinCount,
  onBack,
  className = ""
}) => {
  return (
    <header className={`
      flex justify-between items-center p-6 
      bg-white/80 backdrop-blur-sm shadow-lg relative z-10
      ${className}
    `}>
      <div className="flex items-center gap-4">
        <button
          onClick={onBack}
          className="bg-gradient-to-br from-gray-500 to-gray-600 hover:from-gray-600 hover:to-gray-700 text-white p-3 rounded-full shadow-lg hover:shadow-xl transition-all duration-300 hover:scale-105"
        >
          <ArrowLeft className="w-5 h-5" />
        </button>
        
        <div className="flex items-center gap-4">
          <div className="relative">
            <img src="/images/logo.png" alt="Logo MentaM치tica" className="w-12 h-12 animate-pulse" />
            <div className="absolute -top-1 -right-1 w-4 h-4 bg-yellow-400 rounded-full animate-bounce"></div>
          </div>
          <h1 className="text-3xl font-bold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent">
            MentaM치tica
          </h1>
        </div>
      </div>

      <CoinCounter count={coinCount} />
    </header>
  );
};