import React from 'react';
import { SideModuleButton } from '../atoms/SideModuleButton';

interface Module {
  id: string;
  name: string;
  icon: string;
  color: string;
  isUnlocked: boolean;
}

interface ModuleSidebarProps {
  modules: Module[];
  activeModuleId: string;
  onModuleSelect: (moduleId: string) => void;
  className?: string;
}

export const ModuleSidebar: React.FC<ModuleSidebarProps> = ({
  modules,
  activeModuleId,
  onModuleSelect,
  className = ""
}) => {
  return (
    <div className={`
      fixed left-4 top-1/2 -translate-y-1/2 z-20
      flex flex-col gap-3 p-3 bg-gray-100/80 backdrop-blur-sm
      rounded-3xl shadow-xl border border-white/50
      ${className}
    `}>
      {modules.map((module) => (
        <div key={module.id} className="relative">
          <SideModuleButton
            icon={module.icon}
            isActive={activeModuleId === module.id}
            onClick={() => module.isUnlocked && onModuleSelect(module.id)}
            className={!module.isUnlocked ? 'opacity-50 cursor-not-allowed' : ''}
          />
          
          {/* Lock indicator */}
          {!module.isUnlocked && (
            <div className="absolute -top-1 -right-1 w-5 h-5 bg-gray-600 rounded-full flex items-center justify-center">
              <span className="text-white text-xs">游</span>
            </div>
          )}
          
          {/* Tooltip */}
          <div className="absolute left-full ml-4 top-1/2 -translate-y-1/2 opacity-0 group-hover:opacity-100 transition-opacity duration-300 pointer-events-none">
            <div className="bg-gray-800 text-white px-3 py-2 rounded-lg text-sm whitespace-nowrap shadow-lg">
              {module.name}
              <div className="absolute left-0 top-1/2 -translate-y-1/2 -translate-x-full">
                <div className="w-0 h-0 border-t-4 border-b-4 border-r-4 border-transparent border-r-gray-800"></div>
              </div>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};