import React from 'react';

interface CoinCounterProps {
  count: number;
  className?: string;
}

export const CoinCounter: React.FC<CoinCounterProps> = ({
  count,
  className = ""
}) => {
  return (
    <div className={`flex items-center gap-2 ${className}`}>
      <span className="text-sm font-medium text-gray-700">TUS MONEDAS:</span>
      <div className="flex items-center gap-1">
        <div className="w-8 h-8 bg-gradient-to-br from-yellow-400 to-orange-400 rounded-full flex items-center justify-center shadow-lg">
          <span className="text-white font-bold text-sm">$</span>
        </div>
        <div className="bg-white/90 backdrop-blur-sm rounded-full px-3 py-1 shadow-md">
          <span className="font-bold text-gray-800">{count}</span>
        </div>
      </div>
    </div>
  );
};