'use client';

import React, { useState } from 'react';
import { ModuleHeader } from '../molecules/ModuleHeader';
import { ModuleSidebar } from '../molecules/ModuleSidebar';
import { ModuleContent } from '../molecules/ModuleContent';

interface Module {
  id: string;
  name: string;
  icon: string;
  description: string;
  color: string;
  bgColor: string;
  progress: number;
  stars: number;
  isUnlocked: boolean;
}

interface ModulePageTemplateProps {
  currentModule: Module;
  allModules: Module[];
  coinCount: number;
  onBack: () => void;
  onModuleChange: (moduleId: string) => void;
  onLevelChange: (level: number) => void;
  onVideoPlay: () => void;
}

export const ModulePageTemplate: React.FC<ModulePageTemplateProps> = ({
  currentModule,
  allModules,
  coinCount,
  onBack,
  onModuleChange,
  onLevelChange,
  onVideoPlay
}) => {
  const [currentLevel, setCurrentLevel] = useState(1);
  const maxLevel = 3; // Por defecto 3 niveles por m칩dulo

  const handleLevelChange = (level: number) => {
    setCurrentLevel(level);
    onLevelChange(level);
  };

  // Part칤culas flotantes de fondo
  const FloatingParticle = ({ delay }: { delay: number }) => (
    <div
      className="absolute w-2 h-2 bg-yellow-300 rounded-full opacity-60 animate-bounce"
      style={{
        left: `${Math.random() * 100}%`,
        top: `${Math.random() * 100}%`,
        animationDelay: `${delay}s`,
        animationDuration: `${2 + Math.random() * 2}s`,
      }}
    />
  );

  return (
    <div className="min-h-screen bg-gradient-to-br from-amber-50 via-orange-50 to-yellow-50 relative overflow-hidden">
      {/* Part칤culas flotantes de fondo */}
      <div className="fixed inset-0 pointer-events-none">
        {[...Array(15)].map((_, i) => (
          <FloatingParticle key={i} delay={i * 0.3} />
        ))}
      </div>

      {/* Elementos decorativos de fondo */}
      <div className="fixed top-0 left-0 w-full h-full pointer-events-none z-0">
        <div className="absolute top-20 right-20 w-32 h-32 bg-blue-200 rounded-full opacity-20 blur-2xl animate-pulse"></div>
        <div
          className="absolute bottom-20 left-20 w-40 h-40 bg-pink-200 rounded-full opacity-20 blur-2xl animate-pulse"
          style={{ animationDelay: "1s" }}
        ></div>
        <div
          className="absolute top-1/2 right-1/4 w-24 h-24 bg-green-200 rounded-full opacity-15 blur-xl animate-pulse"
          style={{ animationDelay: "2s" }}
        ></div>
      </div>

      {/* Header */}
      <ModuleHeader
        title={currentModule.name}
        description={currentModule.description}
        coinCount={coinCount}
        onBack={onBack}
      />

      {/* Sidebar */}
      <ModuleSidebar
        modules={allModules}
        activeModuleId={currentModule.id}
        onModuleSelect={onModuleChange}
      />

      {/* Main Content */}
      <ModuleContent
        moduleTitle={currentModule.name}
        moduleDescription={`Mientras m치s niveles completes y lo logres con 3 estrellas obtendr치s m치s monedas. 쮺on qu칠 nivel quieres empezar?`}
        currentLevel={currentLevel}
        maxLevel={maxLevel}
        onLevelChange={handleLevelChange}
        onVideoPlay={onVideoPlay}
        className="ml-24" // Margen para el sidebar
      />

      <style jsx>{`
        @keyframes float {
          0%, 100% { transform: translateY(0px); }
          50% { transform: translateY(-10px); }
        }
        
        .animate-float {
          animation: float 3s ease-in-out infinite;
        }
      `}</style>
    </div>
  );
};