import React from 'react';
import { LevelButton } from '../atoms/LevelButton';
import { VideoPlaceholder } from '../atoms/VideoPlaceholder';
import { Brain } from 'lucide-react';

interface ModuleContentProps {
  moduleTitle: string;
  moduleDescription: string;
  currentLevel: number;
  maxLevel: number;
  onLevelChange: (level: number) => void;
  onVideoPlay: () => void;
  className?: string;
}

export const ModuleContent: React.FC<ModuleContentProps> = ({
  moduleTitle,
  moduleDescription,
  currentLevel,
  maxLevel,
  onLevelChange,
  onVideoPlay,
  className = ""
}) => {
  return (
    <main className={`container mx-auto px-6 py-8 relative z-10 ${className}`}>
      {/* Title Section */}
      <div className="text-center mb-8">
        <h1 className="text-5xl font-bold text-gray-800 mb-4">{moduleTitle}</h1>
        <p className="text-lg text-gray-600 max-w-2xl mx-auto">
          {moduleDescription}
        </p>
      </div>

      {/* Level Selection */}
      <div className="flex justify-center mb-8">
        <div className="flex gap-4">
          {Array.from({ length: maxLevel }, (_, i) => i + 1).map((level) => (
            <LevelButton
              key={level}
              level={level}
              isActive={currentLevel === level}
              onClick={() => onLevelChange(level)}
            />
          ))}
        </div>
      </div>

      {/* Video Section */}
      <div className="max-w-4xl mx-auto">
        <VideoPlaceholder
          title="VIDEO EXPLICATIVO"
          onClick={onVideoPlay}
        />
      </div>

      {/* Additional Info */}
      <div className="text-center mt-8">
        <div className="bg-white/90 backdrop-blur-sm rounded-2xl p-6 shadow-lg max-w-2xl mx-auto">
          <h3 className="text-xl font-bold text-gray-800 mb-2 flex items-center justify-center gap-2">
            <Brain className="w-6 h-6 text-purple-600" />
            춰Prep치rate para aprender!
          </h3>
          <p className="text-gray-600">
            Con este nivel aprender치s conceptos fundamentales que fortalecer치n tu comprensi칩n matem치tica.
          </p>
        </div>
      </div>
    </main>
  );
};